import React, { FC, Ref, memo } from 'react';
import { Content } from '../styleds/root'

type CanvasProps = {
  canvasRef: Ref<HTMLCanvasElement>
}

const Canvas: FC<CanvasProps> = ({ canvasRef }) => {
  return (
    <Content>
      <canvas
        ref={canvasRef}
      />
    </Content>
  )
}

export default memo(Canvas);
