import { IPosition } from '@consts/types';
import React, { FC, useCallback, useMemo } from 'react';
import { ISoundMatrix, Track } from '../launchpad.types';
import {
  MatrixContainer,
  MatrixRow,
  MatrixCell
} from '../styleds/matrix';

interface IMatrixProps {
  track: Track;
  matrix: ISoundMatrix;
  onToggleCell: (position: IPosition) => void;
}

interface IActiveCellMap {
  [row: number]: {
    [cell: number]: number
  }
}

const LaunchpadMatrix: FC<IMatrixProps> = ({
    matrix,
    track,
    onToggleCell
}) => {
  const orderMap = useMemo(() => {
    const keyMap = new Array(matrix.length).fill(null).map((_, i) => [i, {}]);
    const map: IActiveCellMap = Object.fromEntries(keyMap);
    return track.reduce((obj, data, index) => {
      const { position: { x, y } } = data;
      obj[y][x] = index + 1;
      return obj;
    }, map)
  }, [track, matrix]);

  const isCellSelect = useCallback(
    (x: number, y: number) => !!orderMap[y][x],
    [orderMap]
  )

  const onCellClick = useCallback((x: number, y: number) => {
    onToggleCell({ x, y });
  }, [onToggleCell])

  return (
    <MatrixContainer>
      {matrix.map((row, rowIndex) => (
        <MatrixRow key={rowIndex}>
          {row.map((cell, cellIndex) => (
            <MatrixCell
              key={cellIndex}
              isSelect={isCellSelect(cellIndex, rowIndex)}
              color={cell.color}
              onClick={() => onCellClick(cellIndex, rowIndex)}
            >
              {cell.nodeName}
            </MatrixCell>
          ))}
        </MatrixRow>
      ))}
    </MatrixContainer>
  )
}

export default LaunchpadMatrix;