import React, { FC, useCallback, useEffect, useMemo, useState } from 'react';
import { IPosition } from '@consts/types';
import isPositionEqual from '@utils/isPositionEqual'
import MusicFactory from '@utils/MusicFactory'
import { Track } from './launchpad.types'
import { Container } from './styleds/root';
import Matrix from './parts/matrix'

import defaultMatrix from '@consts/matrix';
import soundMap from '@consts/soundMap';
import { Button } from '@material-ui/core';
import styled from "@emotion/styled";

interface LaunchPadProps {
  onMusicChange?: (music: Track) => void
  track?: Track;
}

const CustomButton = styled(Button)`
width: 240px !important;
margin: 0 10px;
background: #fff;
`;

const LaunchPad: FC<LaunchPadProps> = ({ track, onMusicChange }) => {
  const [localTrack, setMusic] = useState<Track>([]);

  useEffect(() => {
    setMusic(() => track ?? []);
  }, [track, setMusic])

  useEffect(() => {
    if (onMusicChange) onMusicChange(localTrack);
  }, [localTrack, onMusicChange])

  const musicFactory = useMemo(() => new MusicFactory(soundMap), []);

  const onPlay = useCallback(() => {
    const musicRoad = localTrack.map((item) => item.sound);
    musicFactory.playSong(musicRoad);
  }, [musicFactory, localTrack]);

  // TODO: refactor this fn
  const onToggleCell = useCallback((position: IPosition) => {
    setMusic((track) => {
      const index = track.findIndex((trackItem) => isPositionEqual(trackItem.position, position));
      if (index > -1) {
        return [
          ...track.slice(0, index),
          ...track.slice(index + 1)
        ]
      } else {
        const { sound } =  defaultMatrix[position.y][position.x]
        return [...track, {
          position,
          sound
        }];
      }
    })
  }, [setMusic])

  return (
    <Container>
      <Matrix
        matrix={defaultMatrix}
        track={localTrack}
        onToggleCell={onToggleCell}
      />
      <CustomButton
        onClick={onPlay}
        color='primary'
        variant="contained"
      >
        Play
      </CustomButton>
    </Container>
  )
}

export default LaunchPad;
