import { throttle } from 'lodash'
import { IPosition, ISize } from '@consts/types'
import convertMouseEventToPosition from '../../utils/convertMouseEventToPosition';
import { CustomColor, CustomGradient } from '../../drawer.types';
import changeColor from '../utils/changeColor';
import ResizeArea from './resizeArea';
import { MutableRefObject } from 'react';

type MouseMoveHandler = (event: MouseEvent) => void;
type TouchMoveHandler = (event: TouchEvent) => void;

const hitOptions = {
	segments: true,
	stroke: true,
	fill: true,
	tolerance: 5
};

export abstract class AbstractFigure {
  protected abstract coreFigure: paper.Path;

  private _isActive: boolean = false;
  private _ResizeArea: ResizeArea | null = null;
  private _lastMovePosition: IPosition | null = null;
  private _isSegmentMove: boolean = true;
  private _moveFigureHandler: MouseMoveHandler | null = null;
  private _touchFigureHandler: TouchMoveHandler | null = null;
  private _moveSegmentHandler: MouseMoveHandler | null = null
  private _touchSegmentHandler: TouchMoveHandler | null = null

  public onClick: VoidFunction | null = null;

  constructor(
    protected paperScope: paper.PaperScope,
    protected isPlayRef: MutableRefObject<boolean>,
    protected playRef: MutableRefObject<(figure: AbstractFigure) => void>,
    protected id: string,
  ) {}


  public get scope() {
    return this.paperScope;
  }

  public get isActive() {
    return this._isActive;
  }

  public set isActive(state: boolean) {
    if (this._isActive === state) return;
    if (!this._ResizeArea) {
      this._ResizeArea = new ResizeArea(this.paperScope, this.coreFigure.bounds);
      this._ResizeArea.onResize = (size, position) => {
        this.resizeFigure(size, position);
      }
    }

    if (this.isCanChangePoints()) {
      this.coreFigure.selected = state;
    }
  
    this._ResizeArea.isShow = state;
    this._isActive = state;
  }

  public bringToFront() {
    this.coreFigure.bringToFront();
    this._ResizeArea?.bringToFront();
  }

  public remove() {
    if (this._moveFigureHandler) {
      window.removeEventListener('mousemove', this._moveFigureHandler)
    };

    if (this._ResizeArea) this._ResizeArea.remove();
    this.coreFigure.remove();
  }

  public getColor() {
    return this.coreFigure.fillColor;
  }

  public setColor(color: CustomColor | CustomGradient) {
    changeColor(this.coreFigure, color)
  }
  
  protected isSmoothFigure() {
    return false;
  }
  
  protected isCanChangePoints() {
    return false;
  }

  protected bindHandlers() {
    this.coreFigure.strokeColor = new this.paperScope.Color(0, 0, 0, Number.EPSILON);
    this.coreFigure.strokeWidth = 5;
    this.coreFigure.selectedColor = new this.paperScope.Color(0, 1, 0);

    this.paperScope.project.view.on('mouseup', (event: paper.MouseEvent) => {
      this.unbindSegmentMoveHandler();

      const { shift: isShiftPress } = event.modifiers;
      if (!this.isActive || !isShiftPress) return;

      const hitResult = this.paperScope.project.hitTest(event.point, hitOptions);
      if (!hitResult) return;

      if (hitResult.item !== this.coreFigure || hitResult.type !== 'segment') return;
      hitResult.segment.remove();
      this._ResizeArea?.updateSize(this.coreFigure.bounds);
      event.stopPropagation();
    })

    this.coreFigure.onMouseUp = (event: paper.MouseEvent) => {
      this.unbindFigureMoveHandler();
    }

    this.coreFigure.onMouseDown = (event: paper.MouseEvent) => {
      if (this.isPlayRef.current) {
        this.playRef.current(this);
        return;
      }


      let mustBindHandler = true;
      if (!this.isActive) {
        this.paperScope.project.activeLayer.selected = false;
        this.isActive = true;
      } else if (this.isCanChangePoints()) {
        const { project } = this.paperScope;
        const hitResult = project.hitTest(event.point, hitOptions);
        if (hitResult) {
          const { shift: isShiftPress } = event.modifiers;
          if (isShiftPress && hitResult.type === 'segment') {
            hitResult.segment.remove();
            this._ResizeArea?.updateSize(this.coreFigure.bounds);
            event.stopPropagation();
            mustBindHandler = false;
          } else if (hitResult.type === 'segment' && !isShiftPress) {
            this.bindSegmentMoveHandler(hitResult.segment);
            mustBindHandler = false;
          } else if (hitResult.type === 'stroke') {
            const position = hitResult.location.index + 1;
            const segment = this.coreFigure.insert(position, event.point)
            if (this.isSmoothFigure()) this.coreFigure.smooth();

            this._ResizeArea?.updateSize(this.coreFigure.bounds);
            this.bindSegmentMoveHandler(segment);
            mustBindHandler = false;
          }
        }
      }


      if (mustBindHandler) {
        const { point: { x, y } } = event;
        this.bindFigureMoveHandler({ x, y });
      }
      this.onClick && this.onClick();
    }
  }

  // Move segment logic

  private bindSegmentMoveHandler(segment: paper.Segment) {
    this._isSegmentMove = true;
    this._moveSegmentHandler = throttle((event: MouseEvent) => {
      const position = convertMouseEventToPosition(event);
      segment.point.x = position.x;
      segment.point.y = position.y;
      this._ResizeArea?.updateSize(this.coreFigure.bounds);
    }, 20)
    this._touchSegmentHandler = throttle((event: TouchEvent) => {
      const position = convertMouseEventToPosition(event);
      segment.point.x = position.x;
      segment.point.y = position.y;
      this._ResizeArea?.updateSize(this.coreFigure.bounds);
    }, 20)
    window.addEventListener('mousemove', this._moveSegmentHandler);
    window.addEventListener('touchmove', this._touchSegmentHandler);
  }

  private unbindSegmentMoveHandler() {
    this._isSegmentMove = false;
    if (!this._moveSegmentHandler || !this._touchSegmentHandler) return;
    
    window.removeEventListener('mousemove', this._moveSegmentHandler);
    window.removeEventListener('touchmove', this._touchSegmentHandler);
    this._moveSegmentHandler = null;
  }

  // Move figure logic

  private bindFigureMoveHandler(position: IPosition) {
    this._lastMovePosition = position;
    this._moveFigureHandler = throttle((event: MouseEvent) => this.moveFigure(event), 20)
    this._touchFigureHandler = throttle((event: TouchEvent) => this.moveFigure(event), 20)
    window.addEventListener('mousemove', this._moveFigureHandler);
    window.addEventListener('touchmove', this._touchFigureHandler);
  }

  private unbindFigureMoveHandler() {
    if (!this._moveFigureHandler || !this._touchFigureHandler) return;
    window.removeEventListener('mousemove', this._moveFigureHandler);
    window.removeEventListener('touchmove', this._touchFigureHandler);
    this._lastMovePosition = null;
  }

  private moveFigure(event: MouseEvent | TouchEvent) {
    if (!this._lastMovePosition) return;
    
    const updatePosition = convertMouseEventToPosition(event);
    const diffX = updatePosition.x - this._lastMovePosition.x;
    const diffY = updatePosition.y - this._lastMovePosition.y;
    this.coreFigure.position.x += diffX;
    this.coreFigure.position.y+= diffY;

    if (this._ResizeArea) this._ResizeArea.shift(diffX, diffY);
    this._lastMovePosition = updatePosition;
  }
  // resize logic

  get isResizing(): boolean {
    if (!this._ResizeArea) return false;
    return this._ResizeArea.isShow;
  }

  set isResizing(state: boolean) {
    if (!this._ResizeArea) {
      this._ResizeArea = new ResizeArea(this.paperScope, this.coreFigure.bounds);
      this._ResizeArea.onResize = (size, position) => {
        this.resizeFigure(size, position);
      }
    }
    this._ResizeArea.isShow = state;
  }

  private resizeFigure(size: ISize, position: IPosition) {
    this.coreFigure.bounds.width = size.width;
    this.coreFigure.bounds.height = size.height;
    this.coreFigure.bounds.x = position.x;
    this.coreFigure.bounds.y = position.y;
  }
}