import React, { FC, MutableRefObject, useCallback, useMemo } from 'react';
import paper from 'paper'
import InputLabel from '@material-ui/core/InputLabel';
import 'react-color-gradient-picker/dist/index.css';
import Launchpad, { Track } from '@common/launchpad';
import { FigureData, CustomColor, CustomGradient } from '../../drawer.types';
import { SidebarContainer } from '../../styleds/sidebar';
import ColorPicker from './color-piker';
import changeColor from '../../entity/utils/changeColor';

type Color = CustomColor | CustomGradient;

type SidebarProps = {
  figureData: FigureData | null;
  rootBackgroundRef: MutableRefObject<paper.Path.Rectangle | null>;
}

const convertToColorToPikerFormat = (color: paper.Color): Color => {
  if (color.type === 'rgb') {
    return {
      kind: 'rgb',
      alpha: color.alpha,
      red: Math.round(color.red*255),
      blue: Math.round(color.blue*255),
      green: Math.round(color.green*255),
    }
  } else {
    const { gradient } = color;
    return {
      kind: 'gradient',
      points: gradient.stops.map((stop) => ({
        alpha: stop.color.alpha,
        red: Math.round(stop.color.red*255),
        blue: Math.round(stop.color.blue*255),
        green: Math.round(stop.color.green*255),
        left: stop.offset*100
      })),
      type: gradient.radial ? 'radial' : 'linear',
    }
  }
}


