const Sidebar:FC<SidebarProps> = ({ figureData, rootBackgroundRef }) => {
  const updateFigureColor = useCallback((color: Color, figureData: FigureData | null) => {
    if (figureData) {
      figureData.figure.setColor(color)
    } else {
      const background = rootBackgroundRef.current;
      if (!background) return;
      changeColor(background, color)
    }
    // changeColor(figureData, color)
    // const paperScope = figureData.figure.scope

    // const figureColor = convertToColorToFigureFormat(color, paperScope);
    // console.log('figureColor ', figureColor);
    // figureData.figure.setColor(figureColor);
  }, [])

  const updateFigureTrack = useCallback((track: Track, figureData) => {
    if (!figureData) return;
    figureData.track = track;
  }, [])

  const transformColor = useMemo(() => {
    if (!figureData) {
      const background = rootBackgroundRef.current;
      if (!background || !background.fillColor) return null;
      return convertToColorToPikerFormat(background.fillColor)
    }
    const color = figureData.figure.getColor();
    return color && convertToColorToPikerFormat(color);
  }, [figureData])

  return (
    <SidebarContainer>
      <InputLabel>Fill Color</InputLabel>
      <div style={{ marginTop: '20px' }}>
        <ColorPicker
          onColorChange={(color) => updateFigureColor(color, figureData)}
          color={transformColor}
        />
      </div>
      {!!figureData && (
        <div style={{ marginTop: '40px' }}>
          <InputLabel style={{ marginBottom: '10px' }}>Sound</InputLabel>
          <Launchpad
            onMusicChange={(track) => updateFigureTrack(track, figureData)}
            track={figureData?.track}
          />
        </div>
      )}
    </SidebarContainer>
  )
}


export default Sidebar
