import React, { FC } from 'react';
import styled from '@emotion/styled';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import SvgIcon from '@material-ui/core/SvgIcon';
import PlayArrowOutlinedIcon from '@material-ui/icons/PlayArrowOutlined';
import StopOutlinedIcon from '@material-ui/icons/StopOutlined';
import StopIcon from '@material-ui/icons/Stop';
import { FigureData, Figures } from '../drawer.types';
import { ReactComponent as ellipseSVG } from '../assets/ellipse.svg';
import { ReactComponent as polygonSVG } from '../assets/polygon.svg';
import { ReactComponent as rectangleSVG } from '../assets/rectangle.svg';
import { ReactComponent as blobSVG } from '../assets/blob.svg';
import { ReactComponent as playSVG } from '../assets/play.svg';

type CreateControlGroupProps = {
  selectFigure: FigureData | null;
  isPlay: boolean;
  playToggle: VoidFunction;
  createFigure: (figure: Figures) => void;
  removeFigure: (figure: FigureData) => void;
}

type CustomIconButtonProps = {
  isDelete?: boolean;
}

const CustomIconButtonGroup = styled.div`
  background: #eee;
`;

const CustomIconButton = styled.div<CustomIconButtonProps>`
  padding: 10px;
  width: 44px;
  height: 44px;
  background: ${({ isDelete }) => isDelete ? '#f44336' : '#eee'};
  color: #fff;
  cursor: pointer;
  &:hover {
    color: ${({ isDelete }) => isDelete ? '#d32f2f' : '#000'};
  }
`;

const CustomIconButtonTop = styled(CustomIconButton)`
  padding-top: 20px;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
`;

const CustomIconButtonButton = styled(CustomIconButton)`
  padding-bottom: 20px;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
`;

const CustomIconButtonEllipse = styled(CustomIconButton)<CustomIconButtonProps>`
  border-radius: 50%;
`;

const CustomIconButtonEllipseDelete = styled(CustomIconButtonEllipse)<CustomIconButtonProps>`
`;

const CustomIconButtonEllipsePlay = styled(CustomIconButtonEllipse)<CustomIconButtonProps>`
  margin-top: 44px;
  color: #000;
`;


const ControlGroupContainer = styled.div`
  position: absolute;
  left: 15px;
  bottom: 50%;
  margin-bottom: -126px;
`;


type ControlSubgroupContainerProps = {
  hasButtonOffset?: boolean;
} 

const ControlSubgroupContainer = styled.div<ControlSubgroupContainerProps>`
  display: flex;
  flex-direction: column;
  margin-bottom: ${(props) => props.hasButtonOffset ? '10px' : '0'};
  
`;

const CreateControlGroup: FC<CreateControlGroupProps> = ({
  selectFigure,
  isPlay,
  playToggle,
  createFigure,
  removeFigure,
}) => (
  <>
    <ControlGroupContainer>
      {!!selectFigure && (
        <ControlSubgroupContainer
          hasButtonOffset={true}
          onClick={() => removeFigure(selectFigure)}
        >
          <CustomIconButtonEllipseDelete isDelete={true}>
            <CloseIcon />
          </CustomIconButtonEllipseDelete>
        </ControlSubgroupContainer>
      )}
      <ControlSubgroupContainer>
        <CustomIconButtonTop onClick={() => createFigure('rectangle')}>
          <SvgIcon component={rectangleSVG} viewBox="0 0 33 33" />
        </CustomIconButtonTop>
        <CustomIconButton onClick={() => createFigure('ellipse')}>
          <SvgIcon component={ellipseSVG} viewBox="0 0 36 36" />
        </CustomIconButton>
        <CustomIconButton onClick={() => createFigure('blob')}>
          <SvgIcon component={blobSVG} viewBox="0 0 42 40" />
        </CustomIconButton>
        <CustomIconButtonButton onClick={() => createFigure('polygon')}>
          <SvgIcon component={polygonSVG} viewBox="0 0 44 42" />
        </CustomIconButtonButton>
      </ControlSubgroupContainer>
      <CustomIconButtonEllipsePlay onClick={playToggle}>
        {!isPlay && <SvgIcon component={playSVG} viewBox="0 0 48.11 48.11" />}
        {isPlay && <StopIcon />}
      </CustomIconButtonEllipsePlay>
    </ControlGroupContainer>
  </>
)

export default CreateControlGroup;