import React, { FC, useState, useEffect, useLayoutEffect, useMemo } from 'react';
import styled from '@emotion/styled';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
// @ts-ignore
import { ColorPicker } from 'react-color-gradient-picker';  
import 'react-color-gradient-picker/dist/index.css';
import { CustomColor, CustomGradient } from '../../drawer.types'

const PickerSelectWrap = styled.div`
  padding: 0 5px 5px;
`;

const ColorPickerWrap = styled.div`
  .ui-color-picker {
    width: 100%;
    margin: 0;
    padding-top: 10px;
    background-color: transparent;
    .input-group {
      display: none;
    }
  }
`;

const defaultColor: CustomColor = {
  kind: 'rgb',
  red: 255,
  green: 0,
  blue: 0,
  alpha: 1,
};

const defaultGradient: any = {
  kind: 'gradient',
  points: [
      {
          left: 0,
          red: 0,
          green: 0,
          blue: 0,
          alpha: 1,
      },
      {
          left: 100,
          red: 255,
          green: 0,
          blue: 0,
          alpha: 1,
      } ],
  degree: 0,
  type: 'linear',
};

type Color = CustomColor | CustomGradient;

type ColorPickerComponentProps = {
  onColorChange: (color: Color) => void;
  color: Color | null;
}

const ColorPickerComponent:FC<ColorPickerComponentProps>  = ({ color, onColorChange }) => {
  const [colorAttrs, setColorAttrs] = useState<Color>(color ?? defaultColor);
  const [pikerIsGradient, setPikerType] = useState<number>(0);
  const [reset, setReset] = useState<boolean>(false);
    
  useEffect(() => {
    if (color) {
      const updatePikerType = Number(color.kind === 'gradient');
      setPikerType(updatePikerType);
      setColorAttrs({ ...color });
    }
    setReset(true);
  }, [color, setColorAttrs, setPikerType, setReset]);
    
  useLayoutEffect(() => {
    setReset(false);
  }, [reset, setReset]);

  const onChange = (color: Color) => {
    setColorAttrs(color);
    onColorChange(color);
  };

  const colorProps: any = useMemo(() => {
    const data: any = {};
    if (!colorAttrs) return data;
    if (pikerIsGradient && colorAttrs.kind === 'gradient') {
      data.gradient = {...colorAttrs};
      delete data.gradient.kind;
    }

    if (!pikerIsGradient && colorAttrs.kind === 'rgb') {
      data.color = {...colorAttrs};
      delete data.color.kind;
    }

    return data;
  }, [colorAttrs, pikerIsGradient])
  
  if (reset) return null;
  
  return (
    <>
      <PickerSelectWrap>
        <Select
          value={pikerIsGradient}
          onChange={(event) => setPikerType(event.target.value)}
          fullWidth={true}
        >
          <MenuItem value={0} >Fill</MenuItem>
          <MenuItem value={1} >Gradient</MenuItem>
        </Select>
      </PickerSelectWrap>
      <div style={{ padding: '10px 0' }}></div>
      <ColorPickerWrap>
        <ColorPicker
          // @ts-ignore
          onStartChange={onChange}
          onChange={onChange}
          onEndChange={onChange}
          isGradient={pikerIsGradient}
          {...colorProps}
        />
      </ColorPickerWrap>
    </>
  )
}


export default ColorPickerComponent