import React, { FC, useCallback, useEffect, useRef, memo, MutableRefObject, useMemo } from 'react';
import { PaperScope, Color } from 'paper'
import { Track } from '@common/launchpad';
import MusicFactory from '@utils/MusicFactory'
import { createFigureFunction, FigureClassUnion, Figures, removeFigureFunction } from './drawer.types'
import { AbstractFigure, Ellipse, Rectangle, Blob, Polygon } from './entity'
import ContentCanvas from './parts/canvas'
import { Container } from './styleds/root'
import soundMap from '@consts/soundMap';

// TODO: WTF?? Find decision
// type FigureMap = {
//   [key in Figures]: typeof AbstractFigure;
// }


type FigureMap = {
  [key in Figures]: FigureClassUnion;
}


type FigureData = {
  figure: AbstractFigure;
  track: Track;
}

type DrawerProps = {
  setSelectFigure: React.Dispatch<React.SetStateAction<FigureData | null>>
  setCanvasReady: VoidFunction;
  createFigureRef: MutableRefObject<createFigureFunction | null>;
  removeFigureRef: MutableRefObject<removeFigureFunction | null>;
  rootBackgroundRef: MutableRefObject<paper.Path.Rectangle | null>;
  isPlayRef: MutableRefObject<boolean>;
}

const figureClassMap: FigureMap = {
  ellipse: Ellipse,
  rectangle: Rectangle,
  polygon: Polygon,
  blob: Blob,
}

const Drawer: FC<DrawerProps> = ({
  setSelectFigure,
  setCanvasReady,
  createFigureRef,
  removeFigureRef,
  rootBackgroundRef,
  isPlayRef
}) => {
  const containerRef = useRef<HTMLDivElement>(null);
  const musicFactory = useMemo(() => new MusicFactory(soundMap), []);
  const canvasRef = useRef<HTMLCanvasElement | null>(null);
  const backgroundRef = useRef<paper.Path.Rectangle | null>(null);
  const scopeRef = useRef(new PaperScope());
  const figureArrRef = useRef<FigureData[]>([]);

  const playRef = useRef<(figure: AbstractFigure) => void>((figure: AbstractFigure) => {
    const data = figureArrRef.current.find((item) => item.figure === figure);
    if (!data) return;
    musicFactory.playSong(data.track.map(item => item.sound));
  });
  


  useEffect(() => {
    if (!canvasRef.current) {
      throw new Error('Expected canvas element missing')
    }

    const scope = scopeRef.current;
    scope.setup(canvasRef.current);

    const background = new scope.Path.Rectangle(scope.view.bounds);
    background.fillColor = new scope.Color(1, 1, 1, 1);
    background.onClick = () => {
      setSelectFigure(null);
      figureArrRef.current.forEach((data) => {
        data.figure.isActive = false;
      })
    };

    backgroundRef.current = background;
    rootBackgroundRef.current = background;
    
    scope.project.view.on('mouseup', (event: paper.MouseEvent) => {
      const hitResult = scope.project.hitTest(event.point, {
        segments: true,
        stroke: true,
        fill: true,
        tolerance: 5
      });
      
      if (hitResult) return;
      setSelectFigure(null);
      figureArrRef.current.forEach((data) => {
        data.figure.isActive = false;
      })
    });

    setCanvasReady()

    const handleResizeWindow = (event: UIEvent) => {
      scope.view.viewSize.width = containerRef.current?.clientWidth || 0;
      scope.view.viewSize.height = containerRef.current?.clientHeight || 0;
      background.bounds.height = containerRef.current?.clientHeight || 0;
      background.bounds.width = containerRef.current?.clientWidth || 0;
      scope.view.update();
    }

    scope.view.viewSize.width = containerRef.current?.clientWidth || 0;
    scope.view.viewSize.height = containerRef.current?.clientHeight || 0;
    background.bounds.height = containerRef.current?.clientHeight || 0;
    background.bounds.width = containerRef.current?.clientWidth || 0;
    scope.view.update();

    window.addEventListener('resize', handleResizeWindow);
    return () => {
      window.removeEventListener('resize', handleResizeWindow)
    }

  }, [setSelectFigure, setCanvasReady])

  const onFigureClick = useCallback((triggerFigure: AbstractFigure) => {
    triggerFigure.bringToFront();
    figureArrRef.current.forEach((data) => {
      if (triggerFigure === data.figure) {
        setSelectFigure(data);
      } else {
        data.figure.isActive = false;
      };
    })
  }, [setSelectFigure])

  const createFigure = useCallback((figure: Figures) => {
    const scope = scopeRef.current;
    if (!scope) return;
    const CreatedClass = figureClassMap[figure];

    const color = new Color(Math.random(), Math.random(), Math.random());
    const point = new scope.Point(80, 80);
    const size = new scope.Size(200, 200);
    const rectangle = new scope.Rectangle(point, size);

    const createdFigure = new CreatedClass(scope, isPlayRef, playRef, rectangle, color, 'test')
    createdFigure.onClick = () => onFigureClick(createdFigure);

    const selectData = { figure: createdFigure, track: [] };
    figureArrRef.current.push(selectData);
  }, [onFigureClick])

  const removeFigure = useCallback((figure: FigureData) => {
    const index = figureArrRef.current.findIndex((item) => item === figure);
    if (index === -1) return;

    const removedFigure = figureArrRef.current[index];
    figureArrRef.current.splice(index, 1);
    removedFigure.figure.remove();
  }, [])

  useEffect(() => {
    createFigureRef.current = createFigure;
    removeFigureRef.current = removeFigure;
  }, [createFigure, removeFigure, createFigureRef, removeFigureRef]);

  return (
    <Container ref={containerRef}>
      <ContentCanvas canvasRef={canvasRef} />
    </Container>
  )
}

export default memo(Drawer);
