import { MutableRefObject } from 'react';
import { AbstractFigure } from './core/abstractFigure'

export class Polygon extends AbstractFigure {
  protected coreFigure: paper.Path;

  constructor(
    paperScope: paper.PaperScope,
    isPlayRef: MutableRefObject<boolean>,
    playRef: MutableRefObject<(figure: AbstractFigure) => void>,
    rectangle: paper.Rectangle,
    color: paper.Color,
    id: string
  ) {
    super(paperScope, isPlayRef, playRef, id);
    this.coreFigure = this.createBlobPath(rectangle);
    this.coreFigure.fillColor = color;
    this.bindHandlers();
  }

  protected isCanChangePoints() {
    return true;
  }

  protected createBlobPath(rectangle: paper.Rectangle, pointCount = 10) {
    const maxRadius = 1;
    // const maxRadius = Math.round((rectangle.size.width * rectangle.size.height)/2);
    const path = new this.paperScope.Path();
    path.closed = true;

    const corePoint = rectangle.point.clone()
    corePoint.x += Math.round(rectangle.width/2);
    corePoint.y += Math.round(rectangle.height/2);

    for (var i = 0; i < pointCount; i++) {
      const delta = new this.paperScope.Point({
        length: (maxRadius * 0.5) + (Math.random() * maxRadius * 0.25),
        angle: (360 / pointCount) * i
      });
      path.add(corePoint.multiply(delta));
    }

    path.position.x = corePoint.x + Math.round(rectangle.width/2);
    path.position.y = corePoint.y + Math.round(rectangle.height/2);
    if (this.isSmoothFigure()) path.smooth();

    return path;
  }
}
