import React, { useCallback, useRef, useState } from 'react';
import { Track } from '@common/launchpad';
import Drawer from './drawer'
import { AbstractFigure } from './entity'
import Sidebar2 from './parts/sidebar'
import { Container } from './styleds/root'
import CreateControlGroup from './parts/create-control-group';
import { Figures, createFigureFunction, removeFigureFunction } from './drawer.types';

type FigureData = {
  figure: AbstractFigure;
  track: Track;
}


const DrawerContainer = () => {
  const [selectFigure, setSelectFigure] = useState<FigureData | null>(null);
  const [isCanvasReady, setCanvasStatus] = useState<boolean>(false);
  const createFigureRef = useRef<createFigureFunction | null>(null);
  const removeFigureRef = useRef<removeFigureFunction | null>(null);
  const backgroundRef = useRef<paper.Path.Rectangle | null>(null);

  const isPlayRef = useRef<boolean>(false);
  const [isPlay, setPlayStatus] = useState<boolean>(false);
  isPlayRef.current = isPlay;

  const createFigure = useCallback((figureType: Figures) => {
    if (!createFigureRef.current) return;
    createFigureRef.current(figureType);
  }, []);
  
  const removeFigure = useCallback(() => {
    if (!removeFigureRef.current || !selectFigure) return;
    removeFigureRef.current(selectFigure);
    setSelectFigure(null);
  }, [selectFigure, setSelectFigure]);
  
  const setCanvasReady = useCallback(() => {
    setCanvasStatus(true);
  }, [setCanvasStatus]);
  
  const togglePlayMode = useCallback(() => {
    if (selectFigure) {
      selectFigure.figure.isActive=false;
    }
    setSelectFigure(null);
    setPlayStatus(!isPlay);
  }, [isPlay, selectFigure, setPlayStatus]);

  return (
    <Container>
      {isCanvasReady && (
        <CreateControlGroup
          isPlay={isPlay}
          selectFigure={selectFigure}
          createFigure={createFigure}
          removeFigure={removeFigure}
          playToggle={togglePlayMode}
        />
      )}
      <Drawer
        setSelectFigure={setSelectFigure}
        setCanvasReady={setCanvasReady}
        createFigureRef={createFigureRef}
        removeFigureRef={removeFigureRef}
        rootBackgroundRef={backgroundRef}
        isPlayRef={isPlayRef}
      />
      {!isPlay && (
        <Sidebar2
          figureData={selectFigure}
          rootBackgroundRef={backgroundRef}
        />
      )}
    </Container>
  )
}

export default DrawerContainer;
